html, body {
  font-optical-sizing: auto;
  font-variation-settings: "wdth" 100;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

ul, li, a {
  color: #000;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

:root {
  --blue-color: #00acdc;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  background-color: #000;
  width: 40px;
  height: 40px;
}

.logo-link {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.nav__list a {
  align-items: center;
  height: 60px;
  display: flex;
}

ul.nav__list li {
  margin-right: 20px;
}

ul.nav__list li:last-child {
  margin-right: 0;
}

ul.nav__list li a.nav_link:hover {
  color: var(--blue-color);
}

.nav_link {
  position: relative;
}

.nav_link.is-active {
  color: var(--blue-color);
}

.nav_link.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
